import CONF from './config'
import Api from '@/utils/api'

const peopleInfo = CONF.peopleInfo.API

export default {
  /**
   * [保存App信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getPeopleInfo(params = {}) {
    const res = await Api.get(peopleInfo['queryCheck'], params)
    return res
  },
  async getMassesInfo(params = {}) {
    const res = await Api.get(peopleInfo['massesInfo'], params)
    return res
  }
}
